.App{
  min-height: 100vh;
  background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%);
}

.search-form{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
  background: lightgray;
  border: none;
  padding: 10px 20px;
  color: whitesmoke;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

